.navbar {
    height: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.2rem;
    position: fixed;
    width: 100%;
    transition: all 0.3s ease;
    z-index: 999;
    background-color: white;
    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
    backdrop-filter: blur(5px);
    -webkit-backdrop-filter: blur(5px);
    border: 1px solid rgba(255, 255, 255, 0.3);
}

.navbar.active {
    background-color: #fff;
    height: 60px;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
}

.navbar-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    padding: 0 50px;
}

.navbar-logo img {
    height: 60px;
}

.navbar-logo {
    color: #fff;
    font-style: italic;
    font-size: 2rem;
    text-decoration: none;
}

.nav-menu {
    display: grid;
    grid-template-columns: repeat(4, auto);
    grid-gap: 10px;
    text-align: center;
    justify-content: end;
    list-style: none;
    margin-right: -50px;
    margin-top: 10px;
    padding: 0;
}

.nav-item {
    height: 60px;
}

.nav-links {
    color: royalblue;
    display: flex;
    align-items: center;
    text-decoration: none;
    padding: 0.5rem 1rem;
    height: 100%;
}

.menu-icon {
    display: none;
    color: royalblue;
    font-size: 1.5rem;
    cursor: pointer;
}



@media screen and (max-width: 960px) {
    .navbar {
        height: 60px;
    }

    .navbar-container {
        padding: 0 30px;
    }

    .navbar-logo {
        font-size: 1.8rem;
    }

    .menu-icon {
        display: block;
    }

    .nav-menu {
        display: flex;
        flex-direction: column;
        position: fixed;
        top: 50px;
        left: -100%;
        opacity: 1;
        transition: all 0.5s ease;
        width: 100%;
        padding: 10px 0;

        background: rgba(255, 255, 255, 0.2);
        border-radius: 16px;
        box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
        backdrop-filter: blur(5px);
        -webkit-backdrop-filter: blur(5px);
        border: 1px solid rgba(255, 255, 255, 0.3);
    }

    .nav-menu.active {
        opacity: 1;
        left: 0;
        z-index: 1;
    }

    .nav-item {
        height: 50px;
        margin-bottom: 10px;
    }

    .nav-links {
        text-align: center;
        padding: 1rem;
        width: 100%;
        display: table;
    }

    .navbar-logo img {
        height: 48px;
    }

}

@media screen and (max-width: 768px) {
    .nav-menu {
        grid-template-columns: none;
        width: 100%;
    }

    .nav-item {
        height: 40px;
    }
}
.product-page {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  /* height: 100vh; */
  width: 100%;
  background-color: #f2f2f2;
  position: absolute;
  margin-top: 80px;
}

.product-page__image-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  max-width: 1200px;
}

.product-page__image-slider {
  position: relative;
}

.product-page__image-slider-button {
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  font-size: 24px;
  font-weight: bold;
  background-color: transparent;
  border: none;
  color: dodgerblue;
  outline: none;
  cursor: pointer;
  transition: all 0.3s ease;
}

.product-page__image-slider-button:hover {
  color: black;
}

.product-page__image-slider-button--prev {
  left: 20px;
}

.product-page__image-slider-button--next {
  right: 20px;
}

.product-page__image {
  width: 500px;
  height: 400px;
  margin-top: 100px;
  object-fit: contain;
}

.product-page__details {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  max-width: 1200px;
  padding: 40px;
  background-color: white;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
}

.product-page__title {
  font-size: 36px;
  font-weight: bold;
  margin-bottom: 20px;
  text-align: center;
}

.product-page__description {
  font-size: 18px;
  margin-bottom: 20px;
  text-align: center;
}

.product-page__price-container {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}

.product-page__price {
  font-size: 24px;
  font-weight: bold;
}

.product-page__discount {
  margin-left: 10px;
  font-size: 18px;
  color: green;
}

.product-page__rating {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}

.product-page__rating-stars {
  font-size: 24px;
  color: #ffc107;
}

.product-page__rating-count {
  margin-left: 10px;
  font-size: 18px;
  color: #a0a0a0;
}

.product-page__brand {
  font-size: 18px;
  color: #a0a0a0;
}

.buybtn button {
  margin: 20px;
  background-color: dodgerblue;
  height: 40px;
  width: 100px;
  color: white;
}

.buybtn button:hover {
  background-color: rgb(17, 123, 228);
}

.product-page__reviews{
  display: flex;
  flex-direction: column;
  justify-content: start;
  align-items:start;
  width: 100%;
  max-width: 1200px;
  padding: 40px;
  background-color: white;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
}

.product-page__review-header {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}

.product-page__review-profile {
  width: 40px;
  height: 40px;
  border-radius: 50%;
