@media (max-width: 767px) {
  .reviewform{
    display: flex;
    flex-direction: column;
  }

  .reviewform input,
  .reviewform textarea,
  .reviewform button {
    width: 100%;
    margin-bottom: 1rem;
  }
}


@media (min-width: 768px) {
  .reviewform {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 1rem;
  }

