  body {
    font-family: Arial, sans-serif;
    font-size: 16px;
    line-height: 1.5;
  }
  
  /* Page Styles */
  .checkout-page {
    max-width: 1200px;
    margin: 0 auto;
    padding: 20px;
  }
  
  /* Header Styles */
  .checkout-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
  }
  
  .checkout-title {
    font-size: 2em;
    font-weight: bold;
  }
  
  /* Section Styles */
  .checkout-section {
    margin-bottom: 40px;
  }
  
  .checkout-section-title {
    margin-bottom: 10px;
    font-size: 1.5em;
    font-weight: bold;
  }
  
  /* Order Summary Styles */
  .order-summary {
    border: 1px solid #ccc;
    padding: 20px;
  }
  
  .order-items {
    list-style: none;
    margin-bottom: 10px;
  }
  
  .order-item {
    display: flex;
    justify-content: space-between;
    margin-bottom: 10px;
  }
  
  .order-item-name {
    flex-grow: 1;
  }
  
  .order-total {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .order-total-label {
    font-weight: bold;
  }
  
  .order-total-price {
    font-weight: bold;
  }
  
  /* Form Styles */
  .delivery-form,
  .payment-form {
    display: flex;
    flex-wrap: wrap;
  }
  
  .delivery-form label,
  .payment-form label {
    flex-basis: 100%;
    margin-bottom: 10px;
    font-weight: bold;
  }
  
  .delivery-form input,
  .payment-form input,
  .delivery-form textarea {
    flex-basis: 100%;
    padding: 10px;
    margin-bottom: 20px;
    border: 1px solid #ccc;
    border-radius: 5px;
  }
  
  .delivery-form input[type="text"]:valid,
  .payment-form input[type="text"]:valid,
  .delivery-form textarea:valid {
    border-color: green;
  }
  
  .delivery-form input[type="text"]:invalid:focus,
  .payment-form input[type="text"]:invalid:focus,
  .delivery-form textarea:invalid:focus {
    border-color: red;
  }
  
  .checkout-button {
    display: block;
    width: 100%;
    max-width: 300px;
    margin: 0 auto;
    padding: 10px;
    font-size: 1.2em;
    font-weight: bold;
    color: #fff;
    background-color: #f00;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  /* Media Queries */
  @media screen and (min-width: 768px) {
    .delivery-form label,
    .payment-form label {
      flex-basis: 50%;
      margin-right: 20px;
    }
  
    .delivery-form input,
    .payment-form input,
    .delivery-form textarea {
      flex-basis: 50%;
    }
  }
  
